@font-face {
    font-family: moon;
    src: url('../../style/fonts/Moon_Light.otf');
}

@font-face {
    font-family: sanFran;
    src: url('../../style/fonts/SanFranciscoDisplay-Ultralight.otf');
}

@font-face {
    font-family: museo;
    src: url('../../style/fonts/MuseoSans_500.otf');
}

.workContainer {
  width: 100% !important;
  height: 100%;
}

.rowContainer {
  width: 100% !important;
  margin:0 !important;
}

.workArea {
  background-color: #F4F3F3;
  height: 100%;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.projectCard {
  max-width: 55% !important;
}

.imageRowSection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  min-height: 50%;
}

.imageContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#uofo {
  background-color: #81172B !important;
}

#nokia {
  background-color: #155ea5 !important;
}

#ciena {
  background-color: #C7232D !important;
}

#ibm {
  background-color: #5EACF7 !important;
}

.expandButton {
  padding: 0 !important;
}

.unexpandedTextContainer {
  padding-bottom: 0 !important;
}

.workName {
  font-size: 20px !important;
}

.workRole {
  font-size: 15px !important;
  font-family: museo !important;
}

.workDuration {
  font-size: 15px !important;
  font-family: museo !important;
}

@media screen and (max-width: 600px) {
  .projectCard {
    max-width: 55% !important;
  }

  .imageRowSection {
    padding: 50px 0 50px 0 !important;
  }
}



