@font-face {
    font-family: moon;
    src: url('../../style/fonts/Moon_Light.otf');
}

@font-face {
    font-family: sanFran;
    src: url('../../style/fonts/SanFranciscoDisplay-Ultralight.otf');
}

@font-face {
    font-family: museo;
    src: url('../../style/fonts/MuseoSans_500.otf');
}

.contactScreenArea {
  width: 100%;
  height: 100%;
  background-color:#F4F3F3;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  overflow: auto;
}

.gridContainer {
  margin: 0 50px 0 50px !important;
}

.textContainer {
  -ms-flex:1 1;
      flex:1 1;
}

.formContainer {
  -ms-flex:1 1;
      flex:1 1;
  margin: 0 50px;
}

.upperText {
  margin: 12px 0 20px 0 !important;
  font-size: 19px !important;
  font-family: sanFran !important;
}

.lowerText {
  margin: 20px 0 0 0 !important;
  font-size: 19px !important;
  font-family: sanFran !important;
}

.textFieldContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.nameField {
  margin-top: 0 !important;
}

.messageField {
  margin-bottom: 25px !important;
}

@media screen and (max-width: 600px){
  .upperText {
    margin: 60px 0 15px 0 !important;
    font-size: 18px !important;
  }

  .lowerText {
    margin: 15px 0 0 0 !important;
    font-size: 18px !important;
  }

  .formGrid {
    padding-top: 0 !important;
  }
}



