@font-face {
    font-family: sanFran;
    src: url('../../style/fonts/SanFranciscoDisplay-Ultralight.otf');
}

@font-face {
    font-family: moon;
    src: url('../../style/fonts/Moon_Light.otf');
}

@font-face {
    font-family: museo;
    src: url('../../style/fonts/MuseoSans_500.otf');
}

.projectsContainer {
  height: 100%;
  width: 100%;
}

.projectContainer {
  overflow-x: hidden;
  width: 100%;
  height: calc(100% - 115px);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.imageClass {
  position: relative;
}

.projectTechContainer {
  height: 25px;
  display: -ms-flexbox;
  display: flex;
  margin-left: 15px;
}

.techName {
  font-family: sanFran;
  font-size: 15px;
  margin: 0 10px 0 0;
  color: white
}

.projectNameContainer {
  margin: 10px 0 0 0;
  height: 35px;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.githubProjectLink {
  text-decoration: none;
  margin-right: 15px;
  -ms-flex: 0 1;
      flex: 0 1;
}

.projectGitHubIcon {
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}

.projectGitHubIcon:hover {
  color: #171515 !important;
}

.projectName {
  font-family: museo;
  color: white;
  margin: 0 0 0 15px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.bottomPaddingLongText {
  position: relative;
  height: 35px;
  padding: 0 0 0 10px;
  font-family: sanFran;
  font-size: 16px;
  font-weight: 1000;
  color: white;
  max-width: 300px;
  bottom: 5%;
}

.bottomPaddingShortText {
  position: relative;
  height: 35px;
  padding: 0 0 0 10px;
  font-family: sanFran;
  font-size: 16px;
  font-weight: 1000;
  color: white;
  max-width: 300px;
  bottom: 1%;
}


@media screen and (max-width: 600px) {
  .bottomPaddingLongText {
    bottom: 10%;
  }
}



